import React, { useState } from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import CssBaseline from '@material-ui/core/CssBaseline';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import Slide from '@material-ui/core/Slide';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import MenuDrawer from './MenuDrawer';
import MenuIcon from '@material-ui/icons/Menu';
import IconButton from '@material-ui/core/IconButton';

function HideOnScroll(props) {
    const { children, window } = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({ target: window ? window() : undefined });

    return (
        <Slide appear={false} direction="down" in={!trigger}>
            {children}
        </Slide>
    );
}

HideOnScroll.propTypes = {
    children: PropTypes.element.isRequired,
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window: PropTypes.func,
};

const useStyles = makeStyles((theme) => ({
    appBar: {
        height: 0,
        minHeight: 0,
    },
    toolBarColor: {
        justifyContent: 'flex-end',
        height: 65,
        backgroundColor: '#3C8A99',
        [theme.breakpoints.down('sm')]: {
            display: 'none'
        },

    },
    toolBar: {
        justifyContent: 'flex-end',
        backgroundColor: 'transparent',
        color: 'white',
        zIndex: 2,
        [theme.breakpoints.down('sm')]: {
            display: 'none'
        },
    },
    phoneToolBar: {
        justifyContent: 'flex-end',
        backgroundColor: '#8E0000',
        color: 'white',
        zIndex: 2,
        [theme.breakpoints.up('sm')]: {
            display: 'none'
        },
    },
    button: {
        marginRight: '10px',
        color: 'white',
        textTransform: 'initial',
        fontSize: 'large',
        padding: '0 10px',
    },
    icon: {
        fontSize: 'xx-large',
        color: 'white'
    }
}));

export default function HideAppBar(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [navbar, setNavbar] = useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    const changeBackground = () => {
        if (window.scrollY >= 200) {
            setNavbar(true);
        } else {
            setNavbar(false);
        }
    }

    window.addEventListener('scroll', changeBackground);


    return (
        <React.Fragment>
            <CssBaseline />
            <HideOnScroll {...props}>

                <AppBar className={classes.appBar}>
                    {navbar
                        ? <Toolbar variant="dense" className={classes.toolBarColor}>
                            <div className="cntButtons">
                                <Button className={classes.button} href="#home">Home</Button>
                                <Button className={classes.button} href="#about">About</Button>
                                <Button className={classes.button} href="#proyects">Proyects</Button>
                                <Button className={classes.button} href="#contact">Contact</Button>
                            </div>
                        </Toolbar>
                        : <Toolbar variant="dense" className={classes.toolBar}>
                            <div className="cntButtons">
                                <Button className={classes.button} href="#home">Home</Button>
                                <Button className={classes.button} href="#about">About</Button>
                                <Button className={classes.button} href="#proyects">Proyects</Button>
                                <Button className={classes.button} href="#contact">Contact</Button>
                            </div>
                        </Toolbar>}
                    {navbar
                    ?<Toolbar className={classes.phoneToolBar}>
                        <IconButton
                            color="inherit"
                            aria-label="open drawer"
                            edge="end"
                            onClick={handleDrawerOpen}
                            fontSize="medium"

                        >
                            <MenuIcon className={classes.icon} />
                        </IconButton>

                    </Toolbar>
                    :<Toolbar className={classes.phoneToolBar} style={{backgroundColor: 'transparent'}}>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        edge="end"
                        onClick={handleDrawerOpen}
                        fontSize="medium"

                    >
                        <MenuIcon className={classes.icon} />
                    </IconButton>

                </Toolbar>
                    }
//                 <MenuDrawer open={open} handleDrawerOpen={handleDrawerOpen} handleDrawerClose={handleDrawerClose} />
                </AppBar>
            </HideOnScroll>
        </React.Fragment>
    );
}

